<template>
  <div class="samecity">
    <!-- 头部搜索+轮播图 -->
    <div class="nav-box">
      <div class="nav-name">詠槑</div>
      <van-search
        v-model="value"
        shape="round"
        @focus="goSearch"
        placeholder="搜索店内好物"
      />
    </div>
    <!-- tab分类 -->
    <div class="classification">
      <div class="tab-box" v-for="(item, index) in FloorData" :key="index">
        <!-- 标题图 -->
        <div>
          <img class="auto-img" :src="item.floor_title.image_src" />
        </div>
        <!-- 内容盒 -->
        <div class="content-box clearfix">
          <div class="fl left-img-box">
            <img
              class="left-img"
              :src="item.product_list[0].image_src"
              @click="pGoSearch(item.product_list[0])"
            />
          </div>
          <div class="fl right-img-box">
            <!-- 右边小图片 -->
            <div class="fl right-mini-box">
              <img
                class="right-mini-img"
                :src="item.product_list[1].image_src"
                @click="pGoSearch(item.product_list[1])"
                alt=""
              />
            </div>
            <div class="fl right-mini-box">
              <img
                class="right-mini-img"
                :src="item.product_list[2].image_src"
                @click="pGoSearch(item.product_list[2])"
                alt=""
              />
            </div>
            <div class="fl right-mini-box">
              <img
                class="right-mini-img"
                :src="item.product_list[3].image_src"
                @click="pGoSearch(item.product_list[3])"
                alt=""
              />
            </div>
            <div class="fl right-mini-box">
              <img
                class="right-mini-img"
                :src="item.product_list[4].image_src"
                @click="pGoSearch(item.product_list[4])"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/samecity.less";
export default {
  name: "SameCity",
  data() {
    return {
      value: "",
      FloorData: [],
    };
  },
  created() {
    this.getFloorData();
  },
  methods: {
    // 获取分类数据
    getFloorData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/home/floordata",
      })
        .then((result) => {
          
          this.$toast.clear();
          let data = result.data.message;
          data.map((v, i) => {
            v.product_list.map((item) => {
              item.navigator_url = item.navigator_url.split("=")[1];
            });
          });
          this.FloorData = data;
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 搜索页面
    goSearch() {
      this.$router.push({ name: "Search" });
    },
    // 图片跳转搜索页面
    pGoSearch(item) {
      this.$router.push({ name: "Search", params: { query: item.navigator_url } });
    },
  },
};
</script>

<style lang="less" scoped></style>
